.cell {
  white-space: pre-wrap;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  line-height: 16px;
  height: 100%;

  &__text {
    display: -webkit-box;
    max-height: 66px;
    overflow: hidden;
    -webkit-line-clamp: 3;
    -moz-box-orient: vertical;
    -webkit-box-orient: vertical;
  }
}
